body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
    margin:0px;
}
.id-card-wrapper {
    height: 100vh;
    width:100%;
    background-image: url(/static/media/wallpapper.f09cc013.jpg);
    display: flex;
}
.id-card {
    flex-basis: 100%;
    max-width: 30em;
    border: 1px solid rgb(97, 245, 245);
    margin: auto;
    color: #fff;
    padding: 1em;
    background-image: url(/static/media/carapils.7c0b4b59.png);
    background-size: contain;
    box-shadow: 0px 0px 3px 1px #a0000c, inset 0px 0px 3px 1px #fff9ff;
}

.fbShareText{
    font-family: 'Orbitron', sans-serif;
    color: #fe0d0d;
    text-shadow: 0px 0px 4px #FFF;
    display: inline;
}
.fbShareContainer{
    display: inline-flex;
}
.marqueeText {
    color: #bb6b41;
}

.profile-row {
    display: flex;
}
.profile-row .dp {
    flex-basis: 33.3%;
    position: relative;
    margin: 24px;
    align-self: center;
}
.profile-row .desc {
    flex-basis: 66.6%;
}

.profile-row .dp img {
    max-width: 100%;
    border-radius: 50%;
    display: block;
    box-shadow: 0px 0px 4px 3px #12a0a0;
}
.profile-row .desc {
    padding: 1em;
}

.profile-row .dp .dp-arc-inner {
    position: absolute;
    width: 100%;
    height: 100%;
    border: 6px solid transparent;
    border-top-color: #0AE0DF;
    border-radius: 50%;
    top: -6px;
    left: -6px;

}
@-webkit-keyframes rotate-clock {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
@keyframes rotate-clock {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
.profile-row .dp .dp-arc-outer {
    position: absolute;
    width: calc(100% + 20px);
    height: calc(100% + 20px);
    border: 6px solid transparent;
    border-bottom-color: #0AE0DF;
    border-radius: 50%;
    top: -16px;
    left: -16px;
}

.profile-row .desc {

    font-family: 'Orbitron', sans-serif;
    color: #fe0d0d;
    text-shadow: 0px 0px 4px #FFF;
    letter-spacing: 1px;
}
.profile-row .desc h1 {
    margin: 0px;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

